.programs {
  margin-top: 22px;

  @media (min-width: $tablet-width) {
    margin-top: 0;
  }

  @media (min-width: $desktop-width) {
    margin-top: 81px;
  }
}

.programs__list {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  max-width: 960px;
  display: flex;
  flex-direction: column;

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto;
    gap: 0 80px;
    max-width: 100%;
  }
}

.programs__item {
  background-color: $basic_grey;
  min-height: 266px;
  margin-bottom: 20px;
  padding: 20px;
  padding-bottom: 22px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  position: relative;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    min-height: 372px;
    margin-bottom: 30px;
    padding: 77px 28px 57px 52px;
  }

  @media (min-width: $desktop-width) {
    min-height: 374px;
    margin-bottom: 0;
    padding: 77px 52px 59px 52px;
  }
}

.programs__icon {
  position: absolute;
}

.programs__item--slim {
  .programs__icon {
    @media (min-width: $tablet-width) {
      width: 200px;
      height: 211px;
      right: 62px;
      top: 77px;
    }

    @media (min-width: $desktop-width) {
      width: 100px;
      height: 106px;
      right: auto;
      top: 41px;
    }
  }
}

.programs__item--pro {
  .programs__icon {
    left: 13px;
    top: 24px;
  }

  @media (min-width: $tablet-width) {
    .programs__icon {
      width: 268px;
      height: 200px;
      left: auto;
      right: 28px;
      top: 91px;
    }

    .programs__description {
      max-width: 54.5%;
    }
  }

  @media (min-width: $desktop-width) {
    .programs__icon {
      width: 134px;
      height: 100px;
      left: 33px;
      top: 47px;
    }

    .programs__description {
      max-width: 100%;
    }
  }
}

.programs__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  margin: 0;
  margin-top: 10px;
  padding-left: 70px;

  @media (min-width: $tablet-width) {
    padding-left: 0;
    margin: 0;
    font-size: 36px;
    line-height: 36px;

    &::before {
      display: none;
    }
  }

  @media (min-width: $desktop-width) {
    padding-left: 162px;
    margin: 0;
    font-size: 36px;
    line-height: 36px;

    &::before {
      display: block;
    }
  }
}

.programs__description {
  font-size: 14px;
  line-height: 18px;
  color: $special_dark-grey;
  margin-bottom: 0;
  margin-top: 30px;
  padding-bottom: 23px;
  border-bottom: 1px solid $basic_grey-4;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 24px;
    margin-top: 24px;
    max-width: 60%;
    border-bottom: none;
    padding-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    margin-top: 73px;
    max-width: 100%;
  }
}

.programs__link {
  font-family: "Oswald", "Arial", sans-serif;
  color: $basic-black;
  font-size: 16px;
  line-height: 16px;
  text-decoration: none;
  text-transform: uppercase;
  margin-top: auto;
  position: relative;
  width: fit-content;
  display: flex;
  align-items: center;

  span {
    margin-right: 15px;
  }

  .programs__icon-arrow-long {
    display: none;
  }

  &:hover {
    .programs__icon-arrow {
      display: none;
    }

    .programs__icon-arrow-long {
      display: block;
    }
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;

    span {
      margin-right: 25px;
    }
  }

  @media (min-width: $desktop-width) {
    font-size: 20px;
    line-height: 30px;

    span {
      margin-right: 27px;
    }
  }
}
