.input-radio-checkbox {
  label {
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;

    position: relative;
    display: block;
    cursor: pointer;
    user-select: none;
    padding-left: 45px;
    padding-top: 6px;
    padding-bottom: 5px;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 30px;
      padding-left: 58px;
    }
  }
}

.input-radio-checkbox > input + label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 30px;
  height: 30px;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    width: 40px;
    height: 40px;
  }
}

.input-radio-checkbox > input:disabled + label {
  opacity: 0.3;
}

.input-radio-checkbox > input:focus + label {
  outline: 2px solid $special_dark-grey;
}
